import React, { useState ,useEffect} from 'react';
import {useHistory} from "react-router-dom";
import RefreshIcon from '@material-ui/icons/Refresh';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import SearchIcon from '@material-ui/icons/Search';

const logo="https://i.ibb.co/MD5DV1q/logo.png";
const Dnavbar = (props) => {
  const [userdata,setuserdata]=useState({
  firstname:"user"
  });
  const history=useHistory();
  const [chat,setchat]=useState(false);
  const [signin,setsignin]=useState(true);
  const [isOpen, setIsOpen] = useState(false);
  const [profile_photo,setprofile_photo]=useState();
  const [profile_status,setprofile_status]=useState();

  const toggle = () => setIsOpen(!isOpen);
   const postdata=async(e)=>{
    
    e.preventDefault();
    
    const res=await fetch("/logout",{
      method:"GET",
      headers:{
        "Content-Type":"application/json"
      },
     
    });
    history.push("/login");


   }

  const getdata=async()=>{
    
    
    const res=await fetch("/getdata",{
      method:"GET",
       headers:{
        Accept:"application/json",
        "Content-Type":"application/json"
      },
      credentials:"include"
     
    });
    const data=await res.json();
    console.log(data);
    setuserdata(data);
    console.log(data);
    console.log(data.profile[0].img);
    setprofile_photo(data.profile[0].img);
   
  

    


   }

    const auth=async(e)=>{
    const link=e.target.value;
    e.preventDefault();
    
    const res=await fetch("/chat",{
      method:"GET",
      headers:{
        Accept:"application/json",
        "Content-Type":"application/json"
      },
      credentials:"include"
     
    });
    console.log(res.status);
    if(res.status==200){
  
    setchat(true);
  
    history.push(link);

    }
    else{
    history.push("/login");
    }


   }
   useEffect(()=>{
   getdata();
   },[]);

  
  return (

    <div>
      <Navbar color="dark" light expand="xl">
      <img src={logo} style={{width:'70px'}}/>
        <NavbarBrand href="/" id="navbar_tab" >DEPRESSOR</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto" navbar>

           <NavItem>
              <NavLink href="#"><input type="text" placeholder="search" id="nav_search"/>
              <button className="btn " id="text_white"><SearchIcon/></button></NavLink>
            </NavItem>
        
            <NavItem>
              <NavLink href="/profile"><button className="btn btn-danger" value="/profile" onClick={auth}>Profile</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/chat"><button className="btn btn-danger" value="/chat" onClick={auth}>Chat</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/selfhealing"><button className="btn btn-danger" value="/selfhealing" onClick={auth}>SelfHealing</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/reliever"><button className="btn btn-danger" value="/reliever" onClick={auth}>Reliever</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/contribution"><button className="btn btn-danger" value="/contribution"onClick={auth}>Contribution</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/story"><button className="btn btn-danger" value="/story" onClick={auth}>Story</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/buy"><button className="btn btn-danger" value="/buy" onClick={auth}>Buy</button></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/cart"><button className="btn btn-danger" value="/cart"onClick={auth}><ShoppingCartIcon/></button></NavLink>
            </NavItem>
           
             <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
               <button className="btn btn-danger"><img style={{width:'25px',height:'25px'}} src={profile_photo}/></button>
              </DropdownToggle>
              <DropdownMenu right>
               <DropdownItem>
                  <NavLink href="/signup">{userdata.firstname}<RefreshIcon/></NavLink>
                </DropdownItem>
                {signin==true?
                <DropdownItem>
                  <NavLink href="/signup">Sign In</NavLink>
                </DropdownItem>:null}
                <DropdownItem>
                 <NavLink href="/login">Login</NavLink>
                </DropdownItem>
                <DropdownItem>
                 <NavLink href="/logout" onClick={postdata}>Logout</NavLink>
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                  
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>

          
           
          </Nav>
        
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Dnavbar;