import React,{useState} from "react";
import Button from '@material-ui/core/Button';
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked';

const Dlifamilymembers=(props)=>{

	const callchat=()=>{
		props.change(props.emails,props.name);
	
	}
	return(<>	
			<div>
				<div style={{float:'left'}}>
					<img style={{width:'30px',borderRadius:'300px'}} src={props.img}/>
				</div>
				<div style={{}}>
					<li><Button id="text_white" onClick={callchat}>{props.name}</Button></li>
				</div>
			</div><br/>
			
			
	</>);
}

export default  Dlifamilymembers;