import React,{useState} from 'react';
import Button from '@material-ui/core/Button';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

const Dchatbutton=(props)=>{
	const [delsender,setdelsender]=useState(1);
	const [delreceive,setdelreceive]=useState(1);
	const delitemreceive=()=>{
		setdelreceive(delreceive+1);
		

	}

	const delitemsender=()=>{
		setdelsender(delsender+1);
		

	}
	const deletemessage=()=>{
		props.outdelete(props.id);

	}
	const deletechat_send=async()=>{
	props.outdelete(props.id);

    const chat=props.text;
    const type="send"
   
    const res=await fetch("/deletechats",{
      method:"POST",
      headers:{
        "Content-Type":"application/json"
      },
      body:JSON.stringify({
        chat,type
      })
    });
	}

	const deletechat_received=async()=>{
	props.outdelete(props.id);

    const chat=props.text;
    const type="receive"
   
    const res=await fetch("/deletechats",{
      method:"POST",
      headers:{
        "Content-Type":"application/json"
      },
      body:JSON.stringify({
        chat,type
      })
    });
	}


	return(<>{delsender%2==0?<Button style={{float:'right',color:'crimson'}} onClick={deletechat_send}><DeleteForeverIcon/></Button>:null}
	{props.types=="send"?<div>
	<Button style={{float:'right'}} id="Dchatmessage" onClick={delitemsender}>{props.text}</Button><br/><br/>
	</div>:
	<div><Button id="Dchatmessage" style={{backgroundColor:'dodgerblue',color:'white'}} onClick={delitemreceive}>{props.text}</Button>
	{delreceive%2==0?<Button style={{float:'left',color:'crimson'}} onClick={deletechat_received}><DeleteForeverIcon/></Button>:null}
	<br/><br/></div>
	}
	</>);
}

export default Dchatbutton;